<template>
    <!-- ----------------------------------------------------------------------------- -->
    <!-- ListsTitlewithSubtitleActiontext -->
    <!-- ----------------------------------------------------------------------------- -->
    <div>
        <v-list-item-subtitle class="text-wrap">
           A list can contain a stack within an action. This is useful when you need to display meta text next to your action item.
        </v-list-item-subtitle>
        <div class="mt-4">
            <v-card
                max-width="500"
                class="mx-auto"
            >
                <v-toolbar
                color="info"
                dark
                >
                <v-app-bar-nav-icon></v-app-bar-nav-icon>

                <v-toolbar-title>Inbox</v-toolbar-title>

                <v-spacer></v-spacer>

                <v-btn icon>
                    <v-icon>mdi-magnify</v-icon>
                </v-btn>

                <v-btn icon>
                    <v-icon>mdi-checkbox-marked-circle</v-icon>
                </v-btn>
                </v-toolbar>

                <v-list two-line>
                <v-list-item-group
                    v-model="selected"
                    multiple
                    active-class="info--text"
                >
                    <template v-for="(item, index) in items">
                    <v-list-item :key="item.title">
                        <template v-slot:default="{ active }">
                        <v-list-item-content>
                            <v-list-item-title v-text="item.title"></v-list-item-title>
                            <v-list-item-subtitle class="text--info" v-text="item.headline"></v-list-item-subtitle>
                            <v-list-item-subtitle v-text="item.subtitle"></v-list-item-subtitle>
                        </v-list-item-content>

                        <v-list-item-action>
                            <v-list-item-action-text v-text="item.action"></v-list-item-action-text>
                            <v-icon
                            v-if="!active"
                            color="grey lighten-1"
                            >
                            mdi-star-outline
                            </v-icon>

                            <v-icon
                            v-else
                            color="yellow"
                            >
                            mdi-star
                            </v-icon>
                        </v-list-item-action>
                        </template>
                    </v-list-item>

                    <v-divider
                        v-if="index + 1 < items.length"
                        :key="index"
                    ></v-divider>
                    </template>
                </v-list-item-group>
                </v-list>
            </v-card>
        </div>
    </div>
</template>

<script>
export default {
  name: "ListsTitlewithSubtitleActiontext",

  data: () => ({
      selected: [2],
      items: [
        {
          action: '15 min',
          headline: 'Brunch this weekend?',
          title: 'Ali Connors',
          subtitle: "I'll be in your neighborhood doing errands this weekend. Do you want to hang out?",
        },
        {
          action: '2 hr',
          headline: 'Summer BBQ',
          title: 'me, Scrott, Jennifer',
          subtitle: "Wish I could come, but I'm out of town this weekend.",
        },
        {
          action: '6 hr',
          headline: 'Oui oui',
          title: 'Sandra Adams',
          subtitle: 'Do you have Paris recommendations? Have you ever been?',
        },
        {
          action: '12 hr',
          headline: 'Birthday gift',
          title: 'Trevor Hansen',
          subtitle: 'Have any ideas about what we should get Heidi for her birthday?',
        },
        {
          action: '18hr',
          headline: 'Recipe to try',
          title: 'Britta Holt',
          subtitle: 'We should eat this: Grate, Squash, Corn, and tomatillo Tacos.',
        },
      ],
  }),
  methods: {
    toggle(index) {
      const i = this.selected.indexOf(index);

      if (i > -1) {
        this.selected.splice(i, 1);
      } else {
        this.selected.push(index);
      }
    }
  }
};
</script>